.header {
  height: 6.6rem;
  z-index: 2;
  background-color: #fff;
  border-bottom: .4rem solid #40c057;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  padding: 0 4.8rem;
  display: grid;
  position: relative;
  box-shadow: 0 0 .3rem #0000003d;
}

.logo {
  height: 42px;
}

.logo:hover, .logo:active {
  transition: all .2s;
  transform: scale(1.1);
}

ul {
  list-style-type: none;
}

.scale-up {
  transition: all .2s;
}

.select-a-quiz {
  margin-top: 3.2rem;
}

.main-nav-button {
  color: #fff;
  border-radius: 10px;
  padding: .8rem;
  font-size: 1.8rem;
  font-weight: 450;
  text-decoration: none;
  transition: all .2s;
}

.menu {
  height: 4rem;
  width: 4rem;
  color: #000;
  grid-area: 1 / 3;
  place-self: center end;
}

.scale-up:hover, .scale-up:active {
  transition: all .2s;
  transform: scale(1.1);
}

